// =============================================================================
// CAIP (Chain Agnostic Improvement Proposals) Utilities
// =============================================================================
// Utilities for parsing, formatting, and validating CAIP identifiers
// CAIP-2: Blockchain ID Specification
// CAIP-19: Asset ID Specification

/**
 * CAIP-2 Network Identifier
 * Format: <namespace>:<reference>
 * Examples:
 * - eip155:1 (Ethereum Mainnet)
 * - eip155:8453 (Base)
 * - eip155:56 (BNB Chain)
 * - eip155:137 (Polygon)
 * - solana:5eykt4UsFv8P8NJdTREpY1vzqKqZKvdp (Solana Mainnet)
 */
export type CAIPNetworkId = string;

/**
 * CAIP-19 Asset Identifier
 * Format: <namespace>:<reference>/<asset_namespace>:<asset_reference>
 * Examples:
 * - eip155:1/erc20:0xA0b86991c6218b36c1d19D4a2e9Eb0cE3606eB48 (USDC on Ethereum)
 * - eip155:8453/erc20:0x833589fCD6eDb6E08f4c7C32D4f71b54bdA02913 (USDC on Base)
 * - eip155:1/slip44:60 (ETH on Ethereum)
 */
export type CAIPAssetId = string;

/**
 * Parsed CAIP-2 Network Identifier
 */
export interface ParsedCAIPNetwork {
  namespace: string; // e.g., 'eip155', 'solana'
  reference: string; // e.g., '8453', '5eykt4UsFv8P8NJdTREpY1vzqKqZKvdp'
  chainId?: number; // For EVM chains, the numeric chain ID
}

/**
 * Parsed CAIP-19 Asset Identifier
 */
export interface ParsedCAIPAsset {
  network: ParsedCAIPNetwork;
  assetNamespace: string; // e.g., 'erc20', 'slip44'
  assetReference: string; // e.g., '0x833589fCD6eDb6E08f4c7C32D4f71b54bdA02913', '60'
  address?: string; // For ERC20 tokens, the contract address
}

/**
 * Parse a CAIP-2 network identifier
 */
export function parseCAIPNetwork(caip: CAIPNetworkId): ParsedCAIPNetwork | null {
  try {
    const [namespace, reference] = caip.split(':');
    if (!namespace || !reference) {
      return null;
    }

    // For EVM chains (eip155), reference is the chain ID
    let chainId: number | undefined;
    if (namespace === 'eip155') {
      chainId = parseInt(reference, 10);
      if (isNaN(chainId)) {
        return null;
      }
    }

    return {
      namespace,
      reference,
      chainId,
    };
  } catch {
    return null;
  }
}

/**
 * Format a CAIP-2 network identifier
 */
export function formatCAIPNetwork(namespace: string, reference: string | number): CAIPNetworkId {
  return `${namespace}:${reference}`;
}

/**
 * Parse a CAIP-19 asset identifier
 */
export function parseCAIPAsset(caip: CAIPAssetId): ParsedCAIPAsset | null {
  try {
    const parts = caip.split('/');
    if (parts.length !== 2) {
      return null;
    }

    const networkPart = parts[0];
    const assetPart = parts[1];

    const network = parseCAIPNetwork(networkPart);
    if (!network) {
      return null;
    }

    const [assetNamespace, assetReference] = assetPart.split(':');
    if (!assetNamespace || !assetReference) {
      return null;
    }

    return {
      network,
      assetNamespace,
      assetReference,
      address: assetNamespace === 'erc20' ? assetReference : undefined,
    };
  } catch {
    return null;
  }
}

/**
 * Format a CAIP-19 asset identifier
 */
export function formatCAIPAsset(
  network: CAIPNetworkId,
  assetNamespace: string,
  assetReference: string
): CAIPAssetId {
  return `${network}/${assetNamespace}:${assetReference}`;
}

/**
 * Convert legacy network name to CAIP-2
 */
export function networkToCAIP(network: string): CAIPNetworkId | null {
  const networkMap: Record<string, CAIPNetworkId> = {
    ethereum: 'eip155:1',
    mainnet: 'eip155:1',
    base: 'eip155:8453',
    'base-mainnet': 'eip155:8453',
    bnb: 'eip155:56',
    'bnb-chain': 'eip155:56',
    'bsc': 'eip155:56',
    polygon: 'eip155:137',
    'polygon-mainnet': 'eip155:137',
    arbitrum: 'eip155:42161',
    'arbitrum-one': 'eip155:42161',
    optimism: 'eip155:10',
    'optimism-mainnet': 'eip155:10',
    avalanche: 'eip155:43114',
    'avalanche-mainnet': 'eip155:43114',
    solana: 'solana:5eykt4UsFv8P8NJdTREpY1vzqKqZKvdp',
    'solana-mainnet': 'solana:5eykt4UsFv8P8NJdTREpY1vzqKqZKvdp',
  };

  const normalized = network.toLowerCase().replace(/_/g, '-');
  return networkMap[normalized] || null;
}

/**
 * Convert CAIP-2 to legacy network name
 */
export function caipToNetwork(caip: CAIPNetworkId): string | null {
  const parsed = parseCAIPNetwork(caip);
  if (!parsed) {
    return null;
  }

  if (parsed.namespace === 'eip155') {
    const chainIdMap: Record<number, string> = {
      1: 'ethereum',
      8453: 'base',
      56: 'bnb',
      137: 'polygon',
      42161: 'arbitrum',
      10: 'optimism',
      43114: 'avalanche',
    };
    return chainIdMap[parsed.chainId!] || null;
  }

  if (parsed.namespace === 'solana') {
    return 'solana';
  }

  return null;
}

/**
 * Get chain ID from CAIP-2 network identifier (EVM only)
 */
export function getChainId(caip: CAIPNetworkId): number | null {
  const parsed = parseCAIPNetwork(caip);
  if (parsed?.namespace === 'eip155' && parsed.chainId) {
    return parsed.chainId;
  }
  return null;
}

/**
 * Check if CAIP network is EVM-compatible
 */
export function isEVMNetwork(caip: CAIPNetworkId): boolean {
  const parsed = parseCAIPNetwork(caip);
  return parsed?.namespace === 'eip155' || false;
}

/**
 * Check if CAIP network is Solana
 */
export function isSolanaNetwork(caip: CAIPNetworkId): boolean {
  const parsed = parseCAIPNetwork(caip);
  return parsed?.namespace === 'solana' || false;
}

/**
 * Validate CAIP-2 network identifier format
 */
export function validateCAIPNetwork(caip: CAIPNetworkId): boolean {
  return parseCAIPNetwork(caip) !== null;
}

/**
 * Validate CAIP-19 asset identifier format
 */
export function validateCAIPAsset(caip: CAIPAssetId): boolean {
  return parseCAIPAsset(caip) !== null;
}

/**
 * Get token address from CAIP-19 asset identifier (ERC20 only)
 */
export function getTokenAddress(caip: CAIPAssetId): string | null {
  const parsed = parseCAIPAsset(caip);
  if (parsed?.assetNamespace === 'erc20') {
    return parsed.address || null;
  }
  return null;
}

/**
 * Convert legacy network + token address to CAIP-19
 */
export function tokenToCAIP(network: string, tokenAddress: string): CAIPAssetId | null {
  const networkCAIP = networkToCAIP(network);
  if (!networkCAIP) {
    return null;
  }
  return formatCAIPAsset(networkCAIP, 'erc20', tokenAddress);
}

