// =============================================================================
// CHAIN REGISTRY
// =============================================================================
// Registry of supported blockchain networks with metadata

import { CAIP_NETWORKS, type CAIPNetworkId } from './caip-utils';

/**
 * Network metadata
 */
export interface NetworkMetadata {
  caip: CAIPNetworkId;
  name: string;
  displayName: string;
  chainId: number | null; // For EVM chains
  nativeCurrency: {
    name: string;
    symbol: string;
    decimals: number;
  };
  rpcUrls?: string[];
  blockExplorers?: {
    name: string;
    url: string;
  }[];
  isTestnet: boolean;
  isEVM: boolean;
  isSolana: boolean;
  supportsEIP1559?: boolean; // For EVM chains
  averageGasPrice?: string; // In wei/gwei
  averageBlockTime?: number; // In seconds
}

/**
 * Chain Registry
 * 
 * Central registry of all supported blockchain networks
 */
export class ChainRegistry {
  private networks: Map<CAIPNetworkId, NetworkMetadata> = new Map();

  constructor() {
    this.registerDefaultNetworks();
  }

  /**
   * Register a network
   */
  register(network: NetworkMetadata): void {
    this.networks.set(network.caip, network);
  }

  /**
   * Get network metadata by CAIP identifier
   */
  get(caip: CAIPNetworkId): NetworkMetadata | undefined {
    return this.networks.get(caip);
  }

  /**
   * Get network by legacy name
   */
  getByLegacyName(name: string): NetworkMetadata | undefined {
    for (const network of this.networks.values()) {
      if (network.name.toLowerCase() === name.toLowerCase()) {
        return network;
      }
    }
    return undefined;
  }

  /**
   * Get all registered networks
   */
  getAll(): NetworkMetadata[] {
    return Array.from(this.networks.values());
  }

  /**
   * Get all EVM networks
   */
  getEVMs(): NetworkMetadata[] {
    return Array.from(this.networks.values()).filter((n) => n.isEVM);
  }

  /**
   * Get all Solana networks
   */
  getSolanas(): NetworkMetadata[] {
    return Array.from(this.networks.values()).filter((n) => n.isSolana);
  }

  /**
   * Check if network is registered
   */
  has(caip: CAIPNetworkId): boolean {
    return this.networks.has(caip);
  }

  /**
   * Register default networks
   */
  private registerDefaultNetworks(): void {
    // Ethereum Mainnet
    this.register({
      caip: CAIP_NETWORKS.ETHEREUM,
      name: 'ethereum',
      displayName: 'Ethereum',
      chainId: 1,
      nativeCurrency: {
        name: 'Ether',
        symbol: 'ETH',
        decimals: 18,
      },
      rpcUrls: ['https://eth.llamarpc.com'],
      blockExplorers: [
        {
          name: 'Etherscan',
          url: 'https://etherscan.io',
        },
      ],
      isTestnet: false,
      isEVM: true,
      isSolana: false,
      supportsEIP1559: true,
      averageGasPrice: '30000000000', // 30 gwei
      averageBlockTime: 12,
    });

    // Base
    this.register({
      caip: CAIP_NETWORKS.BASE,
      name: 'base',
      displayName: 'Base',
      chainId: 8453,
      nativeCurrency: {
        name: 'Ether',
        symbol: 'ETH',
        decimals: 18,
      },
      rpcUrls: ['https://mainnet.base.org'],
      blockExplorers: [
        {
          name: 'BaseScan',
          url: 'https://basescan.org',
        },
      ],
      isTestnet: false,
      isEVM: true,
      isSolana: false,
      supportsEIP1559: true,
      averageGasPrice: '1000000', // 0.001 gwei (very cheap)
      averageBlockTime: 2,
    });

    // BNB Chain
    this.register({
      caip: CAIP_NETWORKS.BNB_CHAIN,
      name: 'bnb',
      displayName: 'BNB Chain',
      chainId: 56,
      nativeCurrency: {
        name: 'BNB',
        symbol: 'BNB',
        decimals: 18,
      },
      rpcUrls: ['https://bsc-dataseed.binance.org'],
      blockExplorers: [
        {
          name: 'BscScan',
          url: 'https://bscscan.com',
        },
      ],
      isTestnet: false,
      isEVM: true,
      isSolana: false,
      supportsEIP1559: false,
      averageGasPrice: '3000000000', // 3 gwei
      averageBlockTime: 3,
    });

    // Polygon
    this.register({
      caip: CAIP_NETWORKS.POLYGON,
      name: 'polygon',
      displayName: 'Polygon',
      chainId: 137,
      nativeCurrency: {
        name: 'MATIC',
        symbol: 'MATIC',
        decimals: 18,
      },
      rpcUrls: ['https://polygon-rpc.com'],
      blockExplorers: [
        {
          name: 'PolygonScan',
          url: 'https://polygonscan.com',
        },
      ],
      isTestnet: false,
      isEVM: true,
      isSolana: false,
      supportsEIP1559: true,
      averageGasPrice: '30000000000', // 30 gwei
      averageBlockTime: 2,
    });

    // Arbitrum One
    this.register({
      caip: CAIP_NETWORKS.ARBITRUM,
      name: 'arbitrum',
      displayName: 'Arbitrum One',
      chainId: 42161,
      nativeCurrency: {
        name: 'Ether',
        symbol: 'ETH',
        decimals: 18,
      },
      rpcUrls: ['https://arb1.arbitrum.io/rpc'],
      blockExplorers: [
        {
          name: 'Arbiscan',
          url: 'https://arbiscan.io',
        },
      ],
      isTestnet: false,
      isEVM: true,
      isSolana: false,
      supportsEIP1559: true,
      averageGasPrice: '100000000', // 0.1 gwei
      averageBlockTime: 0.25,
    });

    // Optimism
    this.register({
      caip: CAIP_NETWORKS.OPTIMISM,
      name: 'optimism',
      displayName: 'Optimism',
      chainId: 10,
      nativeCurrency: {
        name: 'Ether',
        symbol: 'ETH',
        decimals: 18,
      },
      rpcUrls: ['https://mainnet.optimism.io'],
      blockExplorers: [
        {
          name: 'Optimistic Etherscan',
          url: 'https://optimistic.etherscan.io',
        },
      ],
      isTestnet: false,
      isEVM: true,
      isSolana: false,
      supportsEIP1559: true,
      averageGasPrice: '1000000', // 0.001 gwei
      averageBlockTime: 2,
    });

    // Avalanche
    this.register({
      caip: CAIP_NETWORKS.AVALANCHE,
      name: 'avalanche',
      displayName: 'Avalanche',
      chainId: 43114,
      nativeCurrency: {
        name: 'AVAX',
        symbol: 'AVAX',
        decimals: 18,
      },
      rpcUrls: ['https://api.avax.network/ext/bc/C/rpc'],
      blockExplorers: [
        {
          name: 'Snowtrace',
          url: 'https://snowtrace.io',
        },
      ],
      isTestnet: false,
      isEVM: true,
      isSolana: false,
      supportsEIP1559: true,
      averageGasPrice: '25000000000', // 25 nAVAX
      averageBlockTime: 2,
    });

    // Solana Mainnet (placeholder - full support when SDK available)
    this.register({
      caip: CAIP_NETWORKS.SOLANA,
      name: 'solana',
      displayName: 'Solana',
      chainId: null,
      nativeCurrency: {
        name: 'SOL',
        symbol: 'SOL',
        decimals: 9,
      },
      rpcUrls: ['https://api.mainnet-beta.solana.com'],
      blockExplorers: [
        {
          name: 'Solana Explorer',
          url: 'https://explorer.solana.com',
        },
      ],
      isTestnet: false,
      isEVM: false,
      isSolana: true,
      averageBlockTime: 0.4,
    });
  }
}

// Singleton instance
let chainRegistry: ChainRegistry | null = null;

/**
 * Get chain registry instance
 */
export function getChainRegistry(): ChainRegistry {
  if (!chainRegistry) {
    chainRegistry = new ChainRegistry();
  }
  return chainRegistry;
}





